<template>
  <v-form ref="form" v-model="valid" @submit.prevent="onSubmit">
    <v-container>
      <v-row>
        <v-col cols="12">ID: {{measurement.id}}</v-col>
      </v-row>
      <v-row>
        <v-col cols="2">
          <v-text-field
            label="Drag length"
            placeholder="Drag length"
            v-model="measurement.drag_length"
            :rules="rules.drag_length"
            type="number"
            autofocus
          />
        </v-col>
        <v-col cols="2">
          <v-text-field
            label="Arrow speed"
            placeholder="Arrow speed"
            v-model="measurement.arrow_speed"
            :rules="rules.arrow_speed"
            type="number"
          />
        </v-col>
        <v-col cols="2">
          <v-text-field
            label="Berger spring strength"
            placeholder="Berger spring strength"
            v-model="measurement.berger_spring_strength"
            :rules="rules.berger_spring_strength"
            type="number"
          />
        </v-col>
        <v-col cols="6">
          <v-text-field
            label="Substance"
            placeholder="Substance"
            v-model="measurement.substance"
            :rules="rules.substance"
            type="text"
          />
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12">
          <v-btn type="submit" color="primary" class="white--text mr-4" :disabled="!valid">Save</v-btn>
          <v-btn v-show="showCancel" class="mr-4" @click="onCancel">Cancel</v-btn>
        </v-col>
      </v-row>
    </v-container>
  </v-form>
</template>

<script>
import { required, isNumber } from "@/libs/validations";

export default {
  props: ["item", "showCancel"],
  mounted() {
    if (this.item) this.measurement = { ...this.item };
  },
  data() {
    return {
      valid: false,
      measurement: {
        drag_length: "",
        arrow_speed: "",
        berger_spring_strength: "",
        substance: ""
      },
      rules: {
        drag_length: [required, isNumber],
        arrow_speed: [required, isNumber],
        berger_spring_strength: [required, isNumber],
        substance: [required]
      }
    };
  },
  methods: {
    async onSubmit() {
      this.$emit("onSubmit", { ...this.measurement });
      this.resetForm();
    },

    async onCancel() {
      this.$emit("onCancel");
      this.resetForm();
    },

    resetForm() {
      this.$refs.form.resetValidation();
    }
  }
};
</script>
