<template>
  <v-layout row wrap>
    <v-flex xs6 pa-2>
      <Panel color="white" title="Measurement settings">
        <v-container fluid grid-list-xl>
          <v-layout wrap align-center>
            <v-flex xs6 d-flex>
              <v-select
                :items="persons"
                @change="SET_CURRENT_PERSON"
                :value="currentPerson"
                return-object
                item-text="name"
                label="Person"
              ></v-select>
            </v-flex>
            <v-flex xs6 d-flex>
              <v-select
                :items="bow_centers_with_generated_names"
                @change="SET_CURRENT_BOW_CENTER"
                :value="currentBowCenter"
                return-object
                item-text="generated_name"
                item-value="id"
                label="Bow center"
              ></v-select>
            </v-flex>
          </v-layout>
          <v-layout wrap align-center>
            <v-flex xs6 d-flex>
              <v-select
                :items="bow_arms_with_generated_names"
                @change="SET_CURRENT_BOW_ARM"
                :value="currentBowArm"
                return-object
                item-text="generated_name"
                item-value="id"
                label="Bow arms"
              ></v-select>
            </v-flex>
            <v-flex xs6 d-flex>
              <v-select
                :items="bow_arrows_with_generated_names"
                @change="SET_CURRENT_BOW_ARROW"
                :value="currentBowArrow"
                return-object
                item-text="generated_name"
                item-value="id"
                label="Bow arrows"
              ></v-select>
            </v-flex>
          </v-layout>
          <v-layout wrap align-center>
            <v-flex xs12>
              <v-subheader class="pl-0">
                Samples:
                <span class="primary--text">{{ samples }}</span>
              </v-subheader>
              <v-slider
                :value="samples"
                thumb-label
                @change="CHANGE_SAMPLES"
                min="500"
                step="100"
                max="3000"
              ></v-slider>
            </v-flex>

            <MeasurementAdditionalDataForm
              v-if="last_measurement"
              :item="last_measurement"
              :showCancel="false"
              @onSubmit="saveAdditionalData"
            />

            <v-flex xs3 d-flex>
              <v-btn
                v-if="!last_measurement"
                color="primary"
                @click="startMeasurement()"
                :disabled="startButtonDisabled"
              >Start</v-btn>
            </v-flex>
          </v-layout>
        </v-container>
      </Panel>
    </v-flex>
    <v-flex xs6 pa-2>
      <Panel title="Event messages">
        <v-container fluid grid-list-xl>
          <v-list two-line>
            <v-divider></v-divider>
            <template v-for="(message, index) in messagesR">
              <v-list-item :key="`${index}-${message.sender}`" ripple>
                <v-list-item-avatar>
                  <v-icon
                    v-if="message.sender == 'commander'"
                    class="brown lighten-3 white--text"
                  >mdi-access-point-network</v-icon>
                  <v-icon v-else class="blue-grey lighten-3 white--text">mdi-access-point</v-icon>
                </v-list-item-avatar>
                <v-list-item-content>
                  <v-list-item-title>
                    <b
                      v-if="message.sender == 'commander'"
                      class="brown--text text--lighten-3"
                    >{{ message.sender }}</b>
                    <b v-else class="blue-grey--text text--lighten-3">{{ message.sender }}</b>
                  </v-list-item-title>
                  <v-list-item-subtitle class="text--primary">{{ message.phase }}</v-list-item-subtitle>
                  <v-list-item-subtitle>{{ message.samples }}</v-list-item-subtitle>
                </v-list-item-content>

                <v-list-item-action>
                  <v-list-item-action-text></v-list-item-action-text>
                  <v-list-item-action-text>{{message.date.format("YYYY. MM. d. hh:mm:ss")}}</v-list-item-action-text>
                </v-list-item-action>
              </v-list-item>
              <v-divider v-if="index + 1 < messagesR.length" :key="index"></v-divider>
            </template>
          </v-list>
        </v-container>
      </Panel>
    </v-flex>
  </v-layout>
</template>

<script>
import Panel from "@/components/Panel";
import MeasurementAdditionalDataForm from "@/components/forms/MeasurementAdditionalDataForm";
import { mapState, mapGetters, mapActions, mapMutations } from "vuex";

export default {
  mounted() {
    this.$store.dispatch("bowArrowModule/fetchItems");
    this.$store.dispatch("bowArmModule/fetchItems");
    this.$store.dispatch("bowCenterModule/fetchItems");
    this.$store.dispatch("personModule/fetchItems");
  },

  components: {
    Panel,
    MeasurementAdditionalDataForm
  },

  computed: {
    startButtonDisabled() {
      if (
        !!this.currentPerson &&
        !!this.currentBowArrow &&
        !!this.currentBowCenter &&
        !!this.currentBowArm
      )
        return false;
      else return true;
    },

    ...mapGetters("measurementModule", ["messagesR"]),
    ...mapState("measurementModule", ["samples", "last_measurement"]),

    ...mapState("personModule", {
      persons: state => state.items,
      currentPerson: state => state.current
    }),

    ...mapGetters("bowArmModule", ["bow_arms_with_generated_names"]),
    ...mapGetters("bowArrowModule", ["bow_arrows_with_generated_names"]),
    ...mapGetters("bowCenterModule", ["bow_centers_with_generated_names"]),

    ...mapState("bowArrowModule", {
      currentBowArrow: state => state.current
    }),
    ...mapState("bowCenterModule", {
      currentBowCenter: state => state.current
    }),
    ...mapState("bowArmModule", {
      currentBowArm: state => state.current
    })
  },

  methods: {
    ...mapActions("measurementModule", [
      "startMeasurement",
      "saveAdditionalData"
    ]),

    ...mapMutations("personModule", { SET_CURRENT_PERSON: "SET_CURRENT" }),
    ...mapMutations("bowCenterModule", {
      SET_CURRENT_BOW_CENTER: "SET_CURRENT"
    }),
    ...mapMutations("bowArmModule", {
      SET_CURRENT_BOW_ARM: "SET_CURRENT"
    }),
    ...mapMutations("bowArrowModule", {
      SET_CURRENT_BOW_ARROW: "SET_CURRENT"
    }),
    ...mapMutations("measurementModule", ["CHANGE_SAMPLES"])
  }
};
</script>
